@use "global" as *;

.p-profile {
  width: 100%;
}

.p-profile__container {
  width: inherit;
  padding: myrem(79) 0 0 0;
  @include mq(md) {
    padding: myrem(102) 0 0 0;
  }
}

.p-profile__top-image {
  margin: myrem(23) calc(50% - 50vw) 0;
  width: 100vw;
  max-width: none;
  @include mq(md) {
    margin: myrem(32) calc(50% - 50vw) 0;
  }
}

.p-profile__top-image img {
  width: 100%;
  aspect-ratio: 375 / 230;
  @include mq(md) {
    aspect-ratio: 1440 / 480;
  }
}

.p-profile__content {
  width: 100%;
  padding: myrem(10) 0 myrem(60) 0;
  background: pink;
  @include mq(md) {
    padding: myrem(18) 0 0 0;
  }
}

.p-profile__wrapper {
  width: inherit;
  padding: myrem(58) 0 0 0;
  @include mq(md) {
    padding: myrem(62) 0 0 0;
  }
}

.p-profile__slill-wrapper {
  display: flex;
  flex-direction: column;
  gap: myrem(20);
  margin: myrem(55) 0 0 0;
  @include mq(md) {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: auto auto;
    margin: myrem(39) 0 0 0;
  }
}

// coding
.p-profile__coding-group {
  width: inherit;
  background: $white;
  padding: myrem(25) 0 myrem(20);
  @include mq(md) {
    grid-column: 1 / 5;
    grid-row: 1;
    padding: myrem(36) 0 myrem(52);
  }
}

.p-profile__title {
  font-size: myrem(20);
  font-weight: 600;
  line-height: 1;
  letter-spacing: 0.1em;
  color: $blue;
  padding: 0 0 0 myrem(10);
  @include mq(md) {
    font-size: myrem(28);
    padding: 0 0 0 myrem(55);
  }
}

.p-profile__list {
  width: inherit;
  display: flex;
  flex-wrap: wrap;
  row-gap: myrem(38);
  margin: myrem(37) 0 0 0;
  @include mq(md) {
    flex-wrap: nowrap;
    padding: 0 0 0 myrem(55);
    margin: myrem(50) 0 0 0;
    gap: myrem(43);
  }
}

.p-profile__item {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: myrem(11);
  @include mq(md) {
    width: auto;
    gap: myrem(13);
  }
}

.html-icon {
  width: myrem(87);
  aspect-ratio: 87 / 99;
  object-fit: contain;
  @include mq(md) {
    width: myrem(90);
    aspect-ratio: 90 / 103;
  }
}
.css-icon {
  width: myrem(88);
  aspect-ratio: 88 / 99;
  object-fit: contain;
  @include mq(md) {
    width: myrem(90);
    aspect-ratio: 90 / 103;
    margin: 0 0 0 myrem(15);
  }
}
.sass-icon {
  width: myrem(108);
  aspect-ratio: 108 / 80;
  object-fit: contain;
  @include mq(md) {
    width: myrem(129);
    aspect-ratio: 129 / 97;
    margin: myrem(-5) 0 0 myrem(6);
  }
}
.javascript-icon {
  width: myrem(91);
  aspect-ratio: 91 / 91;
  object-fit: contain;
  @include mq(md) {
    width: myrem(115);
    aspect-ratio: 115 / 115;
    margin: myrem(-15) 0 0 myrem(-32);
  }
}
.php-icon {
  width: myrem(158);
  aspect-ratio: 158 / 83;
  object-fit: contain;
  @include mq(md) {
  }
}

.p-profile__name {
  font-size: myrem(14);
  font-weight: 800;
  line-height: 1;
  letter-spacing: 0.1em;
  @include mq(md) {
    font-size: myrem(18);
  }
}
.p-profile__name--l {
  font-size: myrem(16);
  @include mq(md) {
    font-size: myrem(18);
  }
}
// cms
.p-profile-cms__group {
  width: 100%;
  background: $white;
  padding: myrem(24) 0 myrem(32) 0;
  @include mq(md) {
    grid-column: 5 / 7;
    grid-row: 1;
    padding: myrem(36) 0 myrem(20) 0;
  }
}

.p-profile__cms-list {
  width: inherit;
  display: flex;
  justify-content: center;
  margin: myrem(24) 0 0 0;
  @include mq(md) {
    padding: 0;
  }
}

.wordpress-icon {
  width: myrem(110);
  aspect-ratio: 110 / 107;
  object-fit: contain;
  @include mq(md) {
    width: myrem(133);
    aspect-ratio: 133 / 130;
  }
}

// デザイン
.p-profile__design-group {
  width: inherit;
  background: $white;
  padding: myrem(25) 0 myrem(32) 0;
  @include mq(md) {
    grid-column: 1 / 4;
    grid-row: 2;
    padding: myrem(45) 0 myrem(32) 0;
  }
}

.p-profile__design-list {
  width: inherit;
  display: flex;
  flex-wrap: wrap;
  row-gap: myrem(29);
  margin: myrem(24) 0 0 0;
  @include mq(md) {
    padding: 0 0 0 myrem(65);
    margin: myrem(39) 0 0 0;
    gap: myrem(34);
  }
}

.Illustrator-icon {
  width: myrem(82);
  aspect-ratio: 82 / 81;
  object-fit: contain;
  @include mq(md) {
    width: myrem(97);
    aspect-ratio: 97 / 97;
  }
}

.Photoshop-icon {
  width: myrem(82);
  aspect-ratio: 82 / 82;
  object-fit: contain;
