  aspect-ratio: 83 / 82;
  object-fit: contain;
  @include mq(md) {
    width: myrem(98);
    aspect-ratio: 98 / 98;
    margin: 0 0 0 myrem(8);
  }
}

.figma-icon {
  width: myrem(56);
  aspect-ratio: 56 / 84;
  object-fit: contain;
  @include mq(md) {
    width: myrem(67);
    aspect-ratio: 67 / 100;
    margin: 0 0 0 myrem(12);
  }
}

// コミュニケーション
.p-profile__communication-group {
  width: inherit;
