  }
}

.p-profile__communication-list {
  width: inherit;
  display: flex;
  flex-wrap: wrap;
  row-gap: myrem(40);
  margin: myrem(33) 0 0 0;
  @include mq(md) {
    padding: 0 0 0 myrem(62);
    margin: myrem(35) 0 0 0;
    gap: 10px;
  }
}

.chatwork-icon {
  width: myrem(94);
  aspect-ratio: 94 / 94;
  object-fit: contain;
  @include mq(md) {
    width: myrem(94);
    aspect-ratio: 94 / 94;
  }
}

.mail-icon {
  width: myrem(100);
  aspect-ratio: 100 / 100;
  object-fit: contain;
